import {
  bindEvent,
  removeEvent
} from '../utils/common'

let nodeList = []
let clickctx = '$clickoutsideCtx'
let beginClick // 

// 
// 
let isEventBinding = false

function targetMousedown(e) {
  beginClick = e
}

function targetMouseup(e) {
  nodeList.forEach(node => {
    node[clickctx].clickoutsideHandler(e, beginClick)
  })
}

// bindEvent(document, 'mousedown', targetMousedown)
// bindEvent(document, 'mouseup', targetMouseup)

// document.addEventListener('mousedown', targetMousedown)
// document.addEventListener('mouseup', targetMouseup)

export default {
  bind(el, binding, vnode) {
    let id = nodeList.push(el) - 1
    const clickoutsideHandler = (mouseup = {}, mousedown = {}) => {
      if (!vnode.context || // vueDOM
        !mouseup.target ||
        !mousedown.target ||
        el.contains(mouseup.target) || // DOM
        el.contains(mousedown.target) || // DOM
        el === mouseup.target || // DOM
        (vnode.context.popup && // 
          (vnode.context.popup.contains(mouseup.target) || // DOM
            vnode.context.popup.contains(mousedown.target))) // DOM
      ) {
        return
      }

      if (
        binding.expression && // 
        el[clickctx].callbackName && // clickoutside
        vnode.context[el[clickctx].callbackName] // vnode
      ) {
        vnode.context[el[clickctx].callbackName]()
      } else {
        el[clickctx].bindingFn && el[clickctx].bindingFn()
      }
    }

    el[clickctx] = {
      id,
      clickoutsideHandler,
      callbackName: binding.expression,
      callbackFn: binding.value
    }
  },
  update(el, binding) {
    if (!isEventBinding) {
      bindEvent(document, 'mousedown', targetMousedown)
      bindEvent(document, 'mouseup', targetMouseup)
      isEventBinding = true
    }
    el[clickctx].callbackName = binding.expression
    el[clickctx].callbackFn = binding.value
  },
  unbind(el) {
    for (let i = 0, len = nodeList.length; i < len; i++) {
      if (nodeList[i][clickctx].id === el[clickctx].id) {
        nodeList.splice(i, 1)
        break
      }
    }
    removeEvent(document, 'mousedown', targetMousedown)
    removeEvent(document, 'mouseup', targetMouseup)
    isEventBinding = false
  }
}



