<template>
  <div class="pr-message-wrapper">
    <transition name="displacement-fade-show">
      <div class="pr-message"
        :class="['pr-message-' + theme]"
        v-show="isShow">
        <i class="pr-icon icon iconfont"
          :class="['icon-' + theme]"></i>
        <span class="pr-message-content">
          <slot>{{ message }}</slot>
        </span>
      </div>
    </transition>
  </div>
</template>

<script type="text/javascript">
/**
 * Message
 * @example
 * this.alertMsg(content, status, delay)
 * content 
 * status   'info','warning','success','danger'
 * delay   
 */
import '../theme-default/message.less'
export default {
  name: 'pr-message',
  data() {
    return {
      theme: 'success', // 
      isShow: false, // 
      delay: 3000, // 
      visible: false // dom
    }
  },
  watch: {
    /*
     ** visiblefalse1smessagedom
     */
    visible(val) {
      if (!val) {
        this.isShow = false
        setTimeout(() => {
          this.$el.parentNode.removeChild(this.$el)
        }, 1000)
      }
    }
  },
  methods: {
    /*
     ** 
     */
    close() {
      this.visible = false
    },
    /*
     ** 
     */
    startCountDown() {
      setTimeout(() => {
        this.close()
      }, this.delay)
    }
  },
  mounted() {
    /*
     ** messagedom
     */
    if (this.delay) {
      this.visible = true
      this.startCountDown()
    }
  }
}
</script>



